import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Card, CardTitle, CardSubtitle, CardBody } from "reactstrap";
import PropTypes from "prop-types";
import "./style.css";
import { Redirect } from "react-router-dom";
import { logout } from "../actions/authActions";
import { buttonReset } from "../actions/uiActions";

export class Profile extends Component {
  static propTypes = {
    button: PropTypes.bool,
    authState: PropTypes.object.isRequired,
    buttonReset: PropTypes.func.isRequired,
    logout: PropTypes.func.isRequired,
  };

  constructor(props) {
    super(props);

    this.state = { result: 0 };
  }

  onLogout = (e) => {
    e.preventDefault();
    this.props.buttonReset();
    this.props.logout();
  };

  componentDidMount() {
    this.handleGetResult();
  }

  handleGetResult = async () => {
    let res = await fetch(
      "https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=usd"
    )
      .then((response) => response.json())
      .then((data) => {
        const { user } = this.props.authState;
        const balance = Number(user?.balance);

        this.setState({
          result: Number(data?.bitcoin.usd) * balance,
        });
      });
  };

  render() {
    if (!this.props.authState.isAuthenticated) {
      return <Redirect to="/" />;
    }

    const { user } = this.props.authState;

    return (
      <div className="container">
        <div className="main">
          <Card>
            <CardBody>
              <CardTitle>
                <h1>
                  {user ? `Welcome, ${user.name}` : ""}{" "}
                  <span role="img" aria-label="party-popper">
                    üéâ{" "}
                  </span>{" "}
                </h1>
              </CardTitle>
              <br />
              <CardSubtitle>
                Amount <h5>{this.state?.result} </h5>
              </CardSubtitle>
              <br />
              <CardSubtitle>
                <h5>
                  {" "}
                  You are now Logged In{" "}
                  <span role="img" aria-label="clap">
                    üëè{" "}
                  </span>
                </h5>
              </CardSubtitle>
              <br />
              <Button size="lg" onClick={this.onLogout} color="primary">
                Logout
              </Button>
            </CardBody>
          </Card>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  //Maps state to redux store as props
  button: state.ui.button,
  authState: state.auth,
});

export default connect(mapStateToProps, { logout, buttonReset })(Profile);
